import React from 'react';
import Card from './Card';

const CardList = ({robots}) => {
    const cardArray = robots.map((user,i) => {
        return (<Card 
        key={i} 
        id={user.id} 
        name={user.name} 
        email={user.email}
        />
        );
    })
    return(
        <React.Fragment>
            {cardArray}
        </React.Fragment>
    );
}

export default CardList;